<template>
  <div class="detail-info">
    <van-row type="flex" justify="space-between">
        <van-col span="15" class="detail-name">{{ ysname }}</van-col>
    </van-row>
    <span class="yatinfo">{{ yatinfo }}</span>
    <span class="actorinfo">{{ actorinfo }}</span>
    <span class="contentinfo">{{ contentinfo }}</span>

    <van-row type="flex" class="handle4btn" justify="space-around">
        <van-col span="6" class="downbtn" @click="handleDown" v-show="false">
          <van-icon name="down"/>
        </van-col>
        <van-col span="6" class="sharebtn" @click="handleShare">
          <van-icon name="share-o"/>
        </van-col>
        <van-col span="6" class="likebtn" @click="handleLike">
          <van-icon :name="likeIcon"/>{{ likeText }}
        </van-col>
    </van-row>

    <van-row type="flex" justify="space-between" align="middle" class="esplay">
      <van-col span="10" class="detail-estitle"></van-col>  
      <van-col span="13" 
        class="detail-espmark"
        @click="handleEspSlide"
        >
        {{ espmark }}
      </van-col>
      <van-icon name="arrow" />
    </van-row>

    <!-- <div class="detail-sources">
      <van-row type="flex" gutter="10">
        <van-col span="8"
          v-for="(bfq,index) of bfqes" 
          :key="index"
          class="bfqbtn"
          :class="{ 'button-selected': selSourceIndex === index }"
          @click="sourceClick(index)"
          >
          {{ bfq.name }}
        </van-col>
      </van-row>
    </div> -->

    <div class="detail-sources">
        <div class="sourcescroll-wrapper" ref="sourcescroll">
          <div class="sourcescroll-content" :style="sourcescrollContentStyle">
            <div
              v-for="(bfq,index) of bfqes"
              :style="selSourceIndex === index ? bfqSelbtn : bfqbtn"
              :key="index"
              @click="sourceClick(index)"
              >
              {{ bfq.name }}
            </div>
          </div>
        </div>
    </div>

    <!-- <div class="detail-esps">
      <van-row type="flex" gutter="10">
        <van-col span="8"
          v-for="(esp,index) of espes" 
          :key="index"
          class="espbtn"
          :class="{ 'espbtn-selected': selEspIndex === index }"
          @click="espClick(index)"
          >
          {{ esp.t }}
        </van-col>
      </van-row>
    </div> -->

    <div class="detail-esps">
        <div class="espscroll-wrapper" ref="espscroll">
          <div class="espscroll-content" :style="espscrollContentStyle">
            <div
              :style="selEspIndex === index ? espSelcedBtn : espBtn"
              v-for="(esp,index) of espes" 
              :key="index"
              @click="espClick(index)"
              >
              {{ esp.t }}
            </div>
          </div>
        </div>
    </div>
    
    <van-popup
      v-model="gridshow"
      position="bottom"
      :overlay="false"
      :style="{ height: '74%' }"
    >
      <div>
        <van-row type="flex" justify="space-between" align="middle" class="esgridplay">
          <van-col span="10" class="grid-estitle">{{ ysname }}</van-col>  
          <van-col span="10" 
            class="gridclose"
            @click="handleGridClose"
            >
            <van-icon name="cross" />
          </van-col>
        </van-row>

        <div class="detail-espgrid">
          <van-row type="flex" 
            v-for="(outList,outIndex) of espgrid"
            :key="outIndex">
              <van-col
                v-for="esp of outList" 
                :key="esp.index"
                :style="selEspIndex === esp.index ? espSelGridBtn : espGridBtn"
                @click="espClick(esp.index)"
                >
                {{ esp.value.t }}
              </van-col>
          </van-row>
        </div>
      </div>
    </van-popup>

  </div>

</template>

<script>
import { Row, Col, Icon, Popup} from 'vant'
import 'vant/lib/row/style'
import 'vant/lib/col/style'
import 'vant/lib/popup/style'
import 'vant/lib/icon/style'
import { EventBus } from 'common/event-bus.js'
import db from 'common/db.js'
import BScroll from '@better-scroll/core'

function chunkArrayWithIndex(array, chunkSize) {
  let result = [];
  for (let i = 0; i < array.length; i += chunkSize) {
    // map
    const chunkWithIndex = array.slice(i, i + chunkSize).map((item, index) => {
      return { index: i + index, value: item };
    });
    // 
    result.push(chunkWithIndex)
  }
  return result;
}

export default {
  name: 'DetailInfo',
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [Icon.name]:Icon,
    [Popup.name]:Popup,
  },
  props: {
    detail:Object
  },
  data () {
    return {
      selSourceIndex: 0,
      selEspIndex: 0,
      espes: [],
      espgrid:[],
      isLike: 0,
      likeText:'',
      likeIcon:'like-o',
      gridshow:false,
      espWidth: 1,
      espHeight: 1,
      sourceWidth: 1.4,
      sourceHeight: 0.66,
    }
  },
  mounted () {
    EventBus.$on('playNext', this.espClick)
  },
  beforeDestroy() {
    this.bs.destroy()
    this.sourceBS.destroy()
    EventBus.$off('playNext', this.espClick)
  },
  methods : {
    initScroll () {
      this.bs = new BScroll(this.$refs.espscroll, {
        scrollX: true,
        scrollY: false,
        click: true
      })
    },
    initSourceScroll () {
      this.sourceBS = new BScroll(this.$refs.sourcescroll, {
        scrollX: true,
        scrollY: false,
        click: true
      })
    },
    jiaziad() {
      //  console.log(`this.$utils.config.adShow: ${typeof(this.$utils.config.adShow)}`)
       if(this.$utils.config.adShow === 1) {
          window.location.href = 'jdzj://function?showad=1'
       }
    },
    handleDown() {
      
    },
    handleShare () {
      if (this.$utils.config.shareText){
        window.location.href = 'jdzj://function?dakaiwx='+this.$utils.config.shareText
        Toast('')
      }
    },
    handleLike () {
      if (this.isLike === 1) {
        this.isLike = 0
        this.likeText = ''
        this.likeIcon = 'like-o'
        db.vodlike.delete(this.detail.id).then(() => {
          console.log('')
        }).catch((error) => {
          console.error('', error)
        })
      } else {
        this.isLike = 1
        this.likeText = ''
        this.likeIcon = 'like'
        const likeVod = {
          vodId: this.detail.id,
          vodName: this.detail.name,
          cover: this.detail.imgy,
          selected: 0,
          datetime: this.$utils.getNowDateString(),
          year: this.detail.yat,
          director: this.detail.direc,
        }
        db.vodlike.put(likeVod).then(() => {
          console.log('Item was added or replaced successfully!')
        }).catch(e => {
          console.error('Error: ' + e)
        })
      }
    },
    handleEspSlide () {
      this.gridshow = true
    },
    handleGridClose () {
      this.gridshow = false
    },
    sourceClick (index) {
      this.selSourceIndex = index
      this.$utils.sourceIndex = index
      const code = this.bfqes[index].code
      if (code) {
        const esplist = this.$utils.espMap.get(code)
        if(esplist !== undefined && esplist !== null) {
          this.espes = esplist
        } else {
          if (this.detail) {
            this.getBofang(this.detail.id,code)
          }
        }
      }
    },
    espClick (index) {
      this.jiaziad()
      this.selEspIndex = index
      this.$utils.espIndex = index
      const url = this.espes[this.selEspIndex].u
      this.$utils.playTitle = this.ysname + ' ' +this.espes[this.selEspIndex].t
      //
      // console.log(' '+url)
      const uList = this.espes.map(item => item.u)
      EventBus.$emit('changePlayUrl', url,uList,index)
      // this.$store.commit('changePlayUrl', url)
    },
    getBofang (id,code) {
        this.$http.get('/api/bfdz',{
            params: {
             id: id,
             code:code
            }
        }).then((res)=>{
          this.bofangSucc(res,code)
        })
     },
     bofangSucc (res,code) {
      // console.log(res)
      res = res.data
      if(res.code === 1000 && res.data){
        this.espes = res.data
        this.$utils.espMap.set(code,this.espes)
      }
     },
     queryVodLike (vId) {
      db.vodlike.get({vodId: vId})
      .then((vod)=> {
        if (vod) {
          this.isLike = 1
          this.likeText = ''
          this.likeIcon = 'like'
        }
      }).catch(function(error) {
        console.error("", error)
      })
     }
  },
  computed: {
    ysname () {
      if (this.detail) {
        return this.detail.name
      } else {
        return ''
      }
    },
    yatinfo () {
      if (this.detail) {
        const year = this.detail.year === "" ? "" : this.detail.year
        const types = this.detail.type.split(',')
        const type = types.length > 2 ? types.slice(0, 2).join("") : types.join("")
        const yatinfo = year + ''+this.detail.area + '' + type
        this.detail.yat = yatinfo
        return yatinfo
      } else {
        return ''
      }   
    },
    actorinfo () {
      if (this.detail) {
        const directors = this.detail.director.split(',')
        let director = directors.length > 0 ? directors[0] : ""
        director = director === "" ? "" : `${director} `
        let actors = `${this.detail.actor}`.split(',')
        let isChinese = true
        if (actors.length > 0) {
            isChinese = actors[0].length <= 4
        }
        let actor = actors.length > 4 ? actors.slice(0, 4).join("") : actors.join("")
        if (!isChinese) {
            actor = actors.length > 2 ? actors.slice(0, 2).join("") : actors.join("")
        }
        director = `${director} ${actor === "" ? "" : actor}`
        this.detail.direc = director
        return director
      } else {
        return ''
      }
    },
    contentinfo () {
      if (this.detail) {
        return this.detail.content
      } else {
        return ''
      }
    },
    espmark () {
      if (this.detail) {
        return this.detail.remarks
      } else {
        return ''
      }
    },
    bfqes () {
      if (this.detail) {
        return this.detail.bfq
      } else {
        return []
      }
    },
    espscrollContentStyle () {
      const widrem = this.$utils.screenWidth / 50
      let count = 6.5
      const fenxiArr = this.espes.slice(0,3)
      let maxLength = 5
      maxLength = fenxiArr.reduce((max, current) => Math.max(max, current.t.length), 0)
      if(maxLength > 5) {
         count = 4.5
      }
      if (this.$utils.screenWidth > 0) {
        this.espWidth = (widrem - (0.18 * 5)) / count
        if(count <= 4.5){
          this.espHeight = this.espWidth / 1.5
        } else {
          this.espHeight = this.espWidth
        }
      }
      const width = `${(this.espWidth * this.espes.length) + (widrem/1.2)}rem`
      // console.log(`scrollContentStyle- width: ${width}  ${this.espWidth}`)
      return {
        width: width,
        height: this.espHeight + 'rem',
        display: 'inline-block'
      }
    },
    espBtn () {
      // console.log(`espBtn- width: ${this.espWidth}`)
      return {
        width: `${this.espWidth}rem`,
        height: `${this.espHeight}rem`,
        display: 'inline-block',
        marginRight: '.18rem',
        lineHeight: `${this.espHeight}rem`,
        textAlign: 'center',
        borderRadius: '.08rem',
        border: '0 solid #cccccc',
        background: '#eeeeee',
        color: '#666666',
        fontSize: '.2rem',
        userSelect: 'none'
      }
    },
    espSelcedBtn () {
      // console.log(`espSelcedBtn- width: ${this.espWidth}`)
      return {
        width: `${this.espWidth}rem`,
        height: `${this.espHeight}rem`,
        display: 'inline-block',
        marginRight: '.18rem',
        lineHeight: `${this.espHeight}rem`,
        textAlign: 'center',
        borderRadius: '.08rem',
        border: '0 solid #41ae3c',
        background: '#c6dfc8',
        color: '#41ae3c',
        fontSize: '.2rem',
        userSelect: 'none'
      } 
    },
    sourcescrollContentStyle() {
      const widrem = this.$utils.screenWidth / 50
      let count = 4.5
      if (this.$utils.screenWidth > 0) {
        this.sourceWidth = (widrem - (0.18 * 4)) / count
        this.sourceHeight = this.sourceWidth / 2.12
      }
      const width = `${(this.sourceWidth * this.bfqes.length) + (widrem/1.2)}rem`
      // console.log(`sourcescrollContentStyle- width: ${width}  ${this.sourceWidth}`)
      return {
        width: width,
        height: this.sourceHeight + 'rem',
        display: 'inline-block'
      }
    },
    bfqbtn () {
      // console.log(`bfqbtn- width: ${this.sourceWidth}`)
      return {
        width: `${this.sourceWidth}rem`,
        height: `${this.sourceHeight}rem`,
        display: 'inline-block',
        marginRight: '.18rem',
        lineHeight: `${this.sourceHeight}rem`,
        textAlign: 'center',
        borderRadius: '.08rem',
        border: '0 solid #cccccc',
        background: '#eeeeee',
        color: '#666666',
        fontSize: '.2rem',
        userSelect: 'none'
      }
    },
    bfqSelbtn () {
      // console.log(`bfqSelbtn- width: ${this.sourceWidth}`)
      return {
        width: `${this.sourceWidth}rem`,
        height: `${this.sourceHeight}rem`,
        display: 'inline-block',
        marginRight: '.18rem',
        lineHeight: `${this.sourceHeight}rem`,
        textAlign: 'center',
        borderRadius: '.08rem',
        border: '0 solid #41ae3c',
        background: '#c6dfc8',
        color: '#41ae3c',
        fontSize: '.2rem',
        userSelect: 'none'
      } 
    },
    espSelGridBtn () {
      return {
        display: 'inline-block',
        marginRight: '.18rem',
        flex: '0 0 auto',
        width: `${this.espWidth}rem`,
        height: `${this.espHeight}rem`,
        lineHeight: `${this.espHeight}rem`,
        textAlign: 'center',
        borderRadius: '.08rem',
        border: '0 solid #41ae3c',
        color: '#41ae3c',
        background: '#c6dfc8',
        fontSize: '.2rem',
        userselect: 'none'
      } 
    },
    espGridBtn () {
      return {
        display: 'inline-block',
        marginRight: '.18rem',
        flex: '0 0 auto',
        width: `${this.espWidth}rem`,
        height: `${this.espHeight}rem`,
        lineHeight: `${this.espHeight}rem`,
        textAlign: 'center',
        borderRadius: '.08rem',
        border: '0 solid #cccccc', //#bacf65
        background: '#eeeeee',
        color: '#666666',
        fontSize: '.2rem',
        userselect: 'none'
      }
    }
  },
  watch: {
    espscrollContentStyle(newValue){
      this.$nextTick(() => {
        if (this.bs) {
          this.bs.refresh()
          setTimeout(()=>{
            // console.log(`scrollTo: ${this.espWidth * 50 * this.$utils.espIndex}`)
            this.bs.scrollTo(-(this.espWidth * 50 * this.$utils.espIndex),0)
          },1000)
        }
      })
    },
    sourcescrollContentStyle(newValue){
      this.$nextTick(() => {
        if (this.sourceBS) {
          this.sourceBS.refresh()
        }
      })
    },
    detail (newDetail,oldDetail) {
      this.queryVodLike(newDetail.id)
    },
    //computedbfqes
    bfqes () {
      this.sourceClick(this.$utils.sourceIndex)
      this.initSourceScroll()
    },
    // espes  url
    espes () {
      console.log('espClick '+this.$utils.espIndex)
      this.espClick(this.$utils.espIndex)
      this.espgrid = chunkArrayWithIndex(this.espes,6)
      this.initScroll()
    }
  },
}
</script>

