<template>
  <div>
    <detail-header :detail="detail" :playPosMap="playPosMap"></detail-header>
    <detail-info :detail="detail"></detail-info>
  </div>
</template>

<script>
import DetailHeader from './components/DetailHeader.vue'
import DetailInfo from './components/DetailInfo.vue'
import db from 'common/db.js'

export default {
   name: 'Detail',
   components: {
      DetailHeader,
      DetailInfo
   },
   data () {
     return {
        detail:null,
        playPosMap:{}
     }
   },
   methods: {
     queryVodHistory(callback) {
       //+this.$route.params.id   parseInt
       db.vodhistory.get({vodId: +this.$route.params.id})
        .then(function(vod) {
         console.log(`queryVodHistory: ${vod}`)
            if (vod) {
               callback(vod)
            }
         }).catch(function(error) {
            console.error("", error)
       })
     },
     getDetail () {
        this.$http.get('/api/spxq',{
            params: {
             id: this.$route.params.id
            }
        }).then(this.detailSucc)
     },
     detailSucc (res) {
        console.log(res)
        res = res.data
        if (res.code == 1000 && res.data) {
            this.detail = res.data
        }
     },
     nativeJsFunction() {
      this.$router.back(-1)
     }
   },
   mounted () {
      this.$utils.espMap.clear()
      this.$utils.espIndex = 0
      this.$utils.sourceIndex = 0
      this.$utils.playTitle = ''
      this.queryVodHistory((vod)=>{
         // console.log(`this.$utils.espIndex: ${JSON.stringify(vod)}`)
         this.$utils.playTitle = vod.esptitle
         this.$utils.espIndex = vod.playIndex
         this.$utils.sourceIndex = vod.sourceIndex
         this.playPos = vod.playPos
         this.playPosMap[vod.playIndex] = vod.playPos
      })
      this.getDetail()
      window.callbackdetal = this.nativeJsFunction
   }
}
</script>

