import { Plugin } from 'xgplayer'

const { POSITIONS } = Plugin

export default class TopBack extends Plugin {

    // key
    static get pluginName() {
      return 'TopBack'
    }
  
    static get defaultConfig () {
        return {
          // controls
          position: POSITIONS.ROOT_TOP,
          handleBack: function() {
            //  TopBack:{ handleBack:()=>{}} 
          },
          vodName:''
        }
    }
  
    constructor (args) {
      super(args)
    }
  
    beforePlayerInit () {
     // TODO start
    }
  
    afterPlayerInit () {
      // TODO start
    }
  
    afterCreate () {
      const { config } = this  
      this.vodname = this.find('.vodname') 
      this.vodname.innerText = config.vodName
      this.icon = this.find('.divicon')
      // this.icon.insertAdjacentText('beforeend',config.vodName)
      this.onIconClick = (e) => {
        console.log('classicon')
      }
      this.onClick = () => {
        console.log('')
        config.handleBack()
      }
      // .iconclick
      this.bind('.icon', 'click', this.onIconClick)
      // click
      this.bind('click', this.onClick)
      //TODO 
    }
  
    destroy () {
      this.unbind('.icon', 'click', this.onIconClick)
      this.unbind('click', this.onClick)
      this.icon = null
    }

    updateVodName(name) {
      this.vodname = this.find('.vodname') 
      this.vodname.innerText = name
    }
  
    render () {
      return `<div class="topback-plugin" style="display: flex;align-items: center;justify-content: center;">
                <div class="divicon" style="margin-top: .1rem;margin-right:.2rem;">
                    <svg t="1705936805165" 
                    class="icon" 
                    viewBox="0 0 1024 1024" 
                    version="1.1" 
                    xmlns="http://www.w3.org/2000/svg" 
                    p-id="4220" width="25" height="25">
                    <path d="M711.1 928.3c-13.2 0-26.3-5-36.4-15.2L297.8 532.9c-20.1-20.2-20.1-53.1 0-73.3L674.7 79.2c20.1-20.3 52.6-20.3 72.7 0 20.1 20.2 20.1 53.1 0 73.3L406.9 496.2l340.5 343.6c20.1 20.3 20.1 53.1 0 73.3-10 10.1-23.2 15.2-36.3 15.2z" fill="#ffffff" p-id="4221">
                    </path>
                    </svg>
                </div>
                <span class="vodname" style="display:flex;color:white;font-size:.26rem;font-weight:600"></span>
              </div>
      `
    }
  }