<template>
    <div class="player-wrapper">
      <div id="mse"/>
    </div>
</template>

<script>
import Player from 'xgplayer'
import HlsPlugin from 'xgplayer-hls'
import { Events } from 'xgplayer'
import 'xgplayer/dist/index.min.css'
import TopBackPlugin from 'common/XGBackPlugin.js'
import JujiPlugin from 'common/JujiPlugin.js'
import { EventBus } from 'common/event-bus.js'
import db from 'common/db.js'

export default {
  name: 'DetailHeader',
  props: {
    detail:Object,    
    playPosMap:{}
  },
  mounted() {
    this.initPlayer()
    EventBus.$on('changePlayUrl', this.changePlay)
  },
  data () {
    return {
      isfull:false,
      player: null,
      playPos:0,
      urls:[]
    }
  },
  watch: {
  },
  methods: {
    initPlayer() {
      this.player = new Player({
          id: 'mse',
          plugins: [HlsPlugin,TopBackPlugin,JujiPlugin],
          ignores: ['volume'],
          playbackRate: {
            hidePortrait:true,
            list: [
              {
                text: '0.5X',
                rate: 0.5
              }, {
                text: '1X',
                iconText: '',
                rate: 1
              }, {
                text: '1.5X',
                rate: 1.5
              },
              {
                text: '1.75X',
                rate: 1.75
              },
              {
                text: '2X',
                rate: 2
              } 
            ]
          },
          topBack: { // 
            handleBack: ()=> {
              if (this.isfull) {
                this.player.exitCssFullscreen()
              } else {
                this.$router.back(-1)
              }
            },
            vodName:''
          },
          juji: {
            handleJuji: ()=> {
              console.log("handleJujihandleJujihandleJuji")
              this.playNext()
            }
          },
          hls: {
              retryCount: 3, //  3 
              retryDelay: 1000, //  1 
              loadTimeout: 10000, //  10 
              fetchOptions: {
                  mode: 'cors' //  fetch undefined
              },
              preloadTime: 120 // s 30
          },
          playsinline:true,
          autoplay:false,
          fullscreen: {
              useCssFullscreen: true
          },
          url: 'https://dwfef.cdw.mp4',
          fluid:true,
          width:'100%',
          height:'100%',
          // playnext: {
          //   url:videoUrl,
          //   urlList: urls,
          //   idx:self.$utils.espIndex,
          // }
      })
    },
    changePlay(url,urls,index) {
      // console.log('changePlay: '+JSON.stringify(this.playPosMap))
      this.urls = urls
      if (this.player) {
        this.player.resetState()
        this.player.currentTime = 0
        this.playPos = 0
        if(this.playPosMap[index]){
          this.playPos = this.playPosMap[index]
        }
        this.player.config.url = url
        this.player.src = url
        this.player.play()
         //
        this.player.on(Events.LOADED_DATA, () => {
        })
        this.player.on(Events.ENDED, () => {
           this.playNext()
        })
        this.player.on(Events.PAUSE, () => {
          window.location.href = 'jdzj://function?pauseddd=1'
        })
        const topBack = this.player.getPlugin('topBack')
        topBack.updateVodName(this.$utils.playTitle)
        const juji = this.player.getPlugin('juji')
        if(!this.isfull){
          juji.hide()
        }
        // 
        this.player.on(Events.FULLSCREEN_CHANGE, (isFullscreen) => {
            //
            if (isFullscreen) {
                juji.show()
                this.isfull = true
                this.callNativeFunction(1)
            } else {
                juji.hide()
                this.isfull = false
                this.callNativeFunction(0)
            }
        })

        if (this.player) {
          this.player.once('canplay', () => {
            this.player.seek(this.playPos)
          })
        }
      }
    },
    callNativeFunction(full) {
       window.location.href = 'jdzj://function?isHengping='+full
    },
    insertVodHistory() {
      let percentage = 0
      if (this.player) {
        const currentPosition = this.player.currentTime || 0
        const duration = this.player.duration || 1 // 
        percentage = (currentPosition * 100) / duration
      }
      const historyVod = {
        vodId: this.detail.id,
        vodName: this.detail.name,
        percent: percentage,
        cover: this.detail.imgy,
        selected: 0,
        datetime: this.$utils.getNowDateString(),
        esptitle: this.$utils.playTitle,
        playIndex: this.$utils.espIndex,
        playPos: this.player.currentTime,
        sourceIndex: this.$utils.sourceIndex,
        watchTime: this.$utils.formatTimeSeconds(this.player.currentTime),
        year: this.detail.yat,
        director: this.detail.direc,
      }
      db.vodhistory.put(historyVod).then(() => {
        console.log('Item was added or replaced successfully!')
      }).catch(e => {
        console.error('Error: ' + e)
      })
    },
    playNext () {
       if (this.$utils.espIndex >= this.urls.length - 1) {
          this.$utils.espIndex = 0
       } else {
          this.$utils.espIndex += 1
       }
       EventBus.$emit('playNext', this.$utils.espIndex)
    }
  },
  beforeDestroy() {
     this.insertVodHistory()
    // 
    if (this.player) {
      console.log('this.player.destroy()')
      this.player.destroy()
    }
    EventBus.$off('changePlayUrl', this.changePlay)
  }
}
</script>

<style lang="stylus" scoped>
  .player-wrapper
    width 100%
    height: 0
    padding-bottom: 56.25%
    background: #000
    position: relative
/deep/.xgplayer-mobile .xg-top-bar
    position: absolute
    width: 100%
    left: 0
    right: 0
    top: 0

</style>